@import "../../index.css";

.projects {
  display: flex;
  flex-wrap: wrap;
  padding: 0 15%;
  width: 100%;
  height: 100vh;
}

.projects > * {
  flex: 1;
  margin-bottom: 30px;
}

.projects-text-all {
  display: flex;
  flex-direction: column;
  color: #fffffe;
  justify-content: center;
  min-width: 100px;
}

.projects-text-header {
  display: inline-block;
  margin-bottom: 0.75em;
  color: #eebbc3;
  font-family: "Caveat", cursive;
  font-size: var(--heading-fontsize);
  text-align: center;
  font-style: italic;
  padding: 0 10px;
}

/* Media Query */
@media (max-width: 480px) {
  .projects {
    padding: 0 10%;
  }
}
