@import url("https://fonts.googleapis.com/css2?family=Arvo&family=Caveat&family=Lato&family=Montserrat&family=Open+Sans&family=Playfair+Display&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,300;0,400;1,300;1,400&display=swap");

html {
  font-size: 24px;
  scroll-behavior: smooth;
}

@media (max-width: 1920px) {
  html {
    font-size: 20px;
  }
}

@media (max-width: 1440px) {
  html {
    font-size: 18px;
  }
}

@media (max-width: 992px) and (min-width: 768px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 768px) and (min-width: 480px) {
  html {
    font-size: 16px;
  }
}

@media (max-width: 480px) and (min-width: 320px) {
  html {
    font-size: 14px;
  }
}

@media (max-width: 320px) {
  html {
    font-size: 12px;
  }
}

:root {
  --heading-fontsize: 2.675rem;
  --subheading-fontsize: 2.25rem;
  --body-biggest-fontsize: 1.3125rem;
  --body-bigger-fontsize: 1.125rem;
  --body-default-fontsize: 1rem;
  --body-smaller-fontsize: 0.8rem;
  --body-smallest-fontsize: 0.5rem;
}

body {
  margin: 0;
  height: 100%;
  font-family: "Open Sans", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: rgb(35,41,70); */
  background-image: linear-gradient(to right, #1b2138, #161b30);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Change the width and height of the scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

/* Change the color of the scrollbar track */
::-webkit-scrollbar-track {
  background-color: #232946;
}

/* Change the color and style of the scrollbar thumb */
::-webkit-scrollbar-thumb {
  background-color: #eebbc3;
  border-radius: 5px;
}

.slide-in-right {
  animation: slide-in-right 1s ease forwards;
}

.slide-in-left {
  animation: slide-in-left 1s ease forwards;
}

@keyframes slide-in-right {
  from {
    transform: translateX(100%);
  }
  to {
    transform: translateX(0);
  }
}

@keyframes slide-in-left {
  from {
    transform: translateX(-100%);
  }
  to {
    transform: translateX(0);
  }
}
