@import "../../index.css";

.about {
  display: flex;
  flex-wrap: wrap;
  padding: 0 15%;
  width: 100%;
  height: 100vh;
}

.about > * {
  flex: 1;
  margin-bottom: 30px;
}

.about-text-all {
  display: flex;
  flex-direction: column;
  color: #fffffe;
  justify-content: center;
  min-width: 320px;
}

.about-text-header {
  display: inline-block;
  margin-bottom: 0.75em;
  color: #eebbc3;
  font-family: "Caveat", cursive;
  font-size: var(--heading-fontsize);
  text-align: center;
  font-style: italic;
  padding: 0 10px;
}

.about-text-p {
  font-size: var(--body-default-fontsize);
  font-family: "Josefin Sans", sans-serif;
  line-height: 1.5;
}

/* Media Query */
@media (max-width: 1200px) {
  .about {
    align-content: center;
  }
  .about-text-all {
    margin: 20px;
    flex-wrap: wrap;
  }
}

@media (max-width: 480px) {
  .about {
    padding: 0 10%;
    flex-direction: row;
  }

  .about > * {
    margin-bottom: 0;
  }

  .about-text-all {
    min-width: 0;
  }
}
